.cart {
  position: fixed;
  right: 15px;
  top: 105px;
  width: 70px;
  height: 70px;
  background: hsl(0, 0%, 100%);
  border-radius: 50%;
  box-shadow: 0 5px 20px -3px hsla(0, 0%, 0%, 0.5);
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer
}
.cart svg path {
    fill: hsl(358, 91%, 34%);
  }
.cart svg {
    width: 70px;
    height: 70px;
  }


